import React from "react";
import "./Projects.css";
import desktopP1 from "../Contents/desktop(p1).jpg";
import desktopP2 from "../Contents/desktop(p2).jpg";
import desktopP3 from "../Contents/desktop(p3).jpg";
function Projects() {
  const ProjectData = [
    {
      img: desktopP1,
      title: "The Movies Space",
      webLink: "https://dishant-codes.github.io/the-movie-space/",
      githubLink: "https://github.com/dishant-codes/the-movie-space",
    },
    {
      img: desktopP2,
      title: "B.Sc.IT Notes",
      webLink: "https://dishant-codes.github.io/B.Sc.IT/",
      githubLink: "https://github.com/dishant-codes/B.Sc.IT",
    },
    {
      img: desktopP3,
      title: "Discord-Clone",
      webLink: "https://discord-clone-5dffe.web.app/",
      githubLink: "https://github.com/dishant-codes/discord-clone",
    },
  ];

  return (
    <div>
      <section className="projects" id="projects">
        <div className="container">
          {/* <!-- PROJECTS Section Heading--> */}
          <div className="text-center">
            <h2 className="p-title page-section-heading d-inline-block mb-0 fw-bold">
              PROJECTS
            </h2>
          </div>
          {/* <!-- Icon Divider--> */}
          <div className="divider-custom">
            <div className="divider-custom-line"></div>
            <div className="divider-custom-icon">
              <i className="fas fa-star"></i>
            </div>
            <div className="divider-custom-line"></div>
          </div>
        </div>
        {/* <!-- Projects Section Content--> */}

        <div className="container d-flex justify-content-center align-items-center">
          <div className="cards">
            {ProjectData.map((val, ind) => {
              return (
                <>
                  <div className="card">
                    <img className="img-fluid" src={val.img} alt="" />
                    <div className="hover-link">
                      <h4>{val.title}</h4>
                      <div className="link-container">
                        <a
                          href={val.webLink}
                          target="_blank"
                          rel="noreferrer"
                          title="visit"
                          className="text-white"
                        >
                          <svg
                            xmlns="http://www.w3.org/2000/svg"
                            width="30"
                            height="30"
                            fill="currentColor"
                            className="bi bi-link-45deg"
                            viewBox="0 0 16 16"
                          >
                            <path d="M4.715 6.542L3.343 7.914a3 3 0 1 0 4.243 4.243l1.828-1.829A3 3 0 0 0 8.586 5.5L8 6.086a1.001 1.001 0 0 0-.154.199 2 2 0 0 1 .861 3.337L6.88 11.45a2 2 0 1 1-2.83-2.83l.793-.792a4.018 4.018 0 0 1-.128-1.287z" />
                            <path d="M6.586 4.672A3 3 0 0 0 7.414 9.5l.775-.776a2 2 0 0 1-.896-3.346L9.12 3.55a2 2 0 0 1 2.83 2.83l-.793.792c.112.42.155.855.128 1.287l1.372-1.372a3 3 0 0 0-4.243-4.243L6.586 4.672z" />
                          </svg>
                        </a>
                        <a
                          href={val.githubLink}
                          target="_blank"
                          rel="noreferrer"
                          title="Code"
                          className="text-white"
                        >
                          <svg
                            xmlns="http://www.w3.org/2000/svg"
                            width="25"
                            height="25"
                            fill="currentColor"
                            className="bi bi-code-slash"
                            viewBox="0 0 16 16"
                          >
                            <path d="M10.478 1.647a.5.5 0 1 0-.956-.294l-4 13a.5.5 0 0 0 .956.294l4-13zM4.854 4.146a.5.5 0 0 1 0 .708L1.707 8l3.147 3.146a.5.5 0 0 1-.708.708l-3.5-3.5a.5.5 0 0 1 0-.708l3.5-3.5a.5.5 0 0 1 .708 0zm6.292 0a.5.5 0 0 0 0 .708L14.293 8l-3.147 3.146a.5.5 0 0 0 .708.708l3.5-3.5a.5.5 0 0 0 0-.708l-3.5-3.5a.5.5 0 0 0-.708 0z" />
                          </svg>
                        </a>
                      </div>
                    </div>
                  </div>
                </>
              );
            })}
          </div>
        </div>
      </section>
    </div>
  );
}

export default Projects;
